.Scene {
  position: absolute;
  left: 0;
  top: 0;
  text-align: center;
  overflow: hidden;
}

.SceneBackground {
  background-size: cover;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
}


